import React, { Component } from 'react';
import Room from './components/Room';
import './App.css';

class App extends Component {
  initialState = {
    rooms: [
      {
        id: 1,
        title: 'Room 1',
        adults : 1,
        child : 0,
        checked: true
      },
      {
        id : 2,
        title: 'Room 2',
        adults : 1,
        child : 1,
        checked: false
      },
      {
        id : 3,
        title: 'Room 3',
        adults : 1,
        child : 0,
        checked: false
      },
      {
        id : 4,
        title: 'Room 4',
        adults : 1,
        child : 0,
        checked: false
      }
    ]
  }

  state = [];
  constructor(props){
    super(props)
    this.state = localStorage.getItem('roomsSaved') ? JSON.parse(localStorage.getItem('roomsSaved')) : this.initialState
  }

  markSelected = (e, id) => {
    this.setState({ rooms: this.state.rooms.map(room => {
      if(e.target.checked && room.id > 1){
        if(room.id <= id && !room.checked){
          room.checked = true
        }
      }else{
        if(room.id >= id && room.checked){
          room.checked = false
          room.adults = 1
          room.child = 0
        }
      }
      return room;
    })})
  }

  handleAdults = (e, id) => {
    this.setState({ rooms: this.state.rooms.map(room => {
      if(room.id === id){
        room.adults = e.target.value 
      }
      return room
    }) })
  }

  handleChildren = (e, id) => {
    this.setState({ rooms: this.state.rooms.map(room => {
      if(room.id === id)
      { 
        room.child = e.target.value
      }
      return room
    }) })
  }

  saveRooms() {
    localStorage.setItem('roomsSaved', JSON.stringify(this.state));
    console.log('save: '+localStorage.getItem('roomsSaved'));
  }

  clearRooms() {
    localStorage.removeItem('roomsSaved');
  }

  render() {
    return (
      <div className="App">
        <div className="rooms">
          <Room rooms={this.state.rooms} 
            markSelected={this.markSelected}
            handleAdults={this.handleAdults}
            handleChildren={this.handleChildren}/>
        </div>
        <div className="btns">
          <button onClick={(e) => this.saveRooms(e)}>Submit</button>
          <button onClick={this.clearRooms}> Clear </button>
        </div>
      </div>
    );
  }
}

export default App;
