import React, { Component } from 'react';
import PropTypes from 'prop-types';

export class RoomItem extends Component {

  render() {
    const { id, title, adults, children, checked} = this.props.room;
    return (
      <div className="card-container">
          <div className="card">
            <div className="card-head">
              { id > 1 && <input type="checkbox" onChange={(event) => this.props.markSelected(event, id)} checked={checked}/> }
              {title}
            </div>
            <div className="card-body">
              <div className="item">
                <label>Adults <br/> (18+):
                  <br/>
                  <select value={adults} onChange= {(event) => this.props.handleAdults(event, id)} disabled={!checked}>
                    <option value="1">1</option>
                    <option value="2">2</option>
                  </select>
                </label>
              </div>
              <div className="item">
                <label>Children <br/> (0-17): 
                <br/>
                  <select value={children} onChange={(event) => this.props.handleChildren(event, id)} disabled={!checked}>
                    <option value="0">0</option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                  </select>
                </label>
              </div>
            </div>
          </div>
      </div>
    )
  }
}

RoomItem.propTypes = {
    room: PropTypes.object.isRequired
}

export default RoomItem
